import React from "react";
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";
import { biografias } from "../../data/telescope20";

const Biografias = () => {

    const Swaled = withReactContent(Swal)

    const openPost = (post) => {
        Swaled.fire({
            title: post.autor,
            background: '#825ee4',
            html:
            <div>
                {post.embedFrame ?
                <div>
                    {post.embedFrame.map((frame) => {
                        return(
                            <div>{frame}</div>
                        )
                        })}                    
                </div>
                :
                        <iframe width="100%" height="800px" title={post.autor} src={post.embed} frameborder="0"></iframe>
                }
            </div> 
        })
    }

 return(
    <div>
        <div className="telescope-titles">
            <h1>Biografias</h1>
        </div>
        <div className="post-container">
                {biografias.map((post) => {
                    return(
                    <div className="eachPost-container">
                        <h2 className="post-name">{post.autor}</h2>
                        <img onClick={() => openPost(post)} src={post.autorFoto} alt="autor" width="250" height="300" className="post-portada"/>
                    </div>
                    )
                })

                }
            </div>
    </div>
 )
}

export default Biografias