import React, {useState} from "react";
import { NavLink } from "react-router-dom";
import styled from "styled-components";

import Portada2021 from '../telescopestatic/GRUPAL.jpeg'
import Portada2020 from '../telescopestatic/portada2020.jpg'
import withReactContent from "sweetalert2-react-content";
import Swal from "sweetalert2";
import { oldTelescopes } from "../data/oldTelescopes";

const Telescope = () => {   

            
    const [isOpen, setIsOpen] = useState(false);

    const open20 = () => {
        setIsOpen('20');
    };
    const open21 = () => {
        setIsOpen('21');
    };

    const Swaled = withReactContent(Swal);
    const openOldTelescopes = (telescope) => {
        Swaled.fire({
            title: `Edicion ${telescope.edicion}`,
            html: <iframe title={`telescope-${telescope.edicion}`} width="100%" height="800px" src={telescope.embed} frameborder="0"></iframe>
        })
    }

    return(
        <>
            {isOpen === false ?           
            <div>
                <TelescopeTitle>
                    <h1><span>Télescope .</span></h1>
                </TelescopeTitle>
                <TelescopesContainer>
                    <NavLink to="/telescopeDO/inicio">
                        <EachTelescopeContainer onClick={open21}>
                            <h3>Edicion 84</h3>
                                <img  src={Portada2021} alt="edicion-84" />
                            <p>Grado 5º 2021</p>
                        </EachTelescopeContainer>
                    </NavLink>
                    <NavLink to="/telescopeDO/inicio20">
                        <EachTelescopeContainer onClick={open20}>
                            <h3>Edicion 83</h3>
                                <img  src={Portada2020} alt="edicion-83" />
                            <p>Grado 5º 2020</p>
                        </EachTelescopeContainer>
                    </NavLink>
                    {oldTelescopes.map((telescope) =>{
                        return(
                            <EachTelescopeContainer onClick={() => openOldTelescopes(telescope)}>
                                <h3>Edicion {telescope.edicion}</h3>
                                    <img  src={telescope.portada} alt={`edicion-${telescope.edicion}`} />
                                <p>Grado 5º {telescope.fecha}</p>
                            </EachTelescopeContainer>
                            )
                        })
                    }
                </TelescopesContainer>

            </div>
            :
            <>
            {isOpen === '21' ?
            <div className="telescope21-routes-container">
                <li className="li-telescope21">
                    <ul> <NavLink to='/telescopeDO/inicio'>Inicio</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/caligramas'>Caligramas</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/moifamille'>Moi et ma famille</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/pandemia'>Transformandome en pandemia</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/nosescriben'>Nos escriben</NavLink></ul>                    
                    <ul> <NavLink to='/telescopeDO/planlector'>Plan lector</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/mihistoria'>Mi historia</NavLink></ul>
                    <ul> <NavLink to='/telescopeDO/stopmotion'>Stop motion</NavLink></ul>
                </li>
            </div>
            :
            <>
            {isOpen === '20' ?
                        <div className="telescope21-routes-container">
                        <li className="li-telescope21">
                            <ul> <NavLink to='/telescopeDO/inicio20'>Inicio</NavLink></ul>
                            <ul> <NavLink to='/telescopeDO/biografias20'>Biografias</NavLink></ul>
                            <ul> <NavLink to='/telescopeDO/comic20'>Comics</NavLink></ul>
                        </li>
                    </div>
                    :
                    <></>
            }
            </>
            }
            </>
            }
        </>
    )
}


const EachTelescopeContainer = styled.div`
    font-family: 'Roboto', sans-serif;
    cursor: pointer;
    text-align: center;
    img{
        width: 250px;
        height: 200px;
        border: 2px solid #000;
        border-radius: 20%;
    }
    p{
        margin: 0;
        text-decoration: none;
        color: black;
    }
    h3{
        margin: 30px 0 10px 0;
        color: #000000;
        text-shadow: 2px 2px 0 #bcbcbc, 4px 4px 0 #9c9c9c;
        font-size: 1.5em; 
        border-bottom: 2.5px solid #FFFFFF;
    }
    a{
        text-decoration: none;
    }
`
const TelescopesContainer = styled.div`
    width: 75%;
    margin: 0 auto;
    justify-content: space-around;
    flex-wrap: wrap;
    align-items: center;
    display: flex;
`

const TelescopeTitle = styled.div`
    text-align: center;
    margin-bottom: 50px;
    span{
        position: relative;
        font-family: 'Roboto', sans-serif;
        text-decoration: underline;
        color: #FFFFFF;
        text-shadow: 1px 1px 1px black;
    }
    span::after{
        content: " ";
        border-left: 2px solid black;
        background-color: #e18aeb;
        right: 0;
        width: 130%;
        height: 100%;
        position: absolute;
        animation: telescope-title-animation 3s alternate infinite steps(10);
    }
    @keyframes telescope-title-animation{
        from{
            width: 100%;
        }
        to{
            width: 0%;
        }
    }
`

export default Telescope