import React,  {useState} from "react";
import styled from "styled-components";
import {postsExp, postsExpm, postsInd, postsProy} from '../data/ciclosData'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faClock, faUser} from "@fortawesome/free-solid-svg-icons";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import Portada2021 from '../telescopestatic/GRUPAL.jpeg'



const Ciclos = () => {
    const Swaled = withReactContent(Swal)
    const [ciclo, cambiarCiclo] = useState('')


    const openPost = (post) => {
        let startsWithHttps = post.embedLink.startsWith('https')



        if(startsWithHttps){
            Swaled.fire({
                background: '#649DC1',
                html: 
                    <div class="swal-post-container">
                        <h1 class="title-swal">{post.title}</h1>
                        <span class="date-author_container-swal">
                            <p class="post_date-swal">{post.date}</p>
                            <p class="post_author-swal">{post.author}</p>
                        </span>
                        <p>{post.description}</p>
                            <img src={post.image} alt="portada" width="auto" height="250px"/>
                        <a href={post.embedLink} target="blank_">{post.embedLink}</a>
                    </div>
            })
        }else if(post.contentImg.length > 1){
            Swaled.fire({
                background: '#649DC1',
                html:
                <div class="swal-post-container">
                    <h1 class="title-swal">{post.title}</h1>
                    <span class="date-author_container-swal">
                        <p class="post_date-swal">{post.date}</p>
                        <p class="post_author-swal">{post.author}</p>
                    </span>
                    <p>{post.description}</p>
                    <div>
                    <div>
                             <div>
                                {post.contentImg.map((image, index) => {
                                    return(
                                        <img className="post-images" src={image} alt="imagen"/>
                                    )
                                })}
                            </div>
                        </div>                    
                    </div>
                    {post.embedLink}
                </div>
            })
        } 
        else{
            Swaled.fire({
                background: '#649DC1',
                html:
                <div class="swal-post-container">
                    <h1 class="title-swal">{post.title}</h1>
                <span class="date-author_container-swal">
                    <p class="post_date-swal"> {post.date}</p>
                    <p class="post_author-swal"> {post.author}</p>
                </span>
                    <p>{post.description}</p>
                    <div id="embed-container" dangerouslySetInnerHTML={{__html: post.embedLink}}>
                    </div>
                </div>
            })
        }
    }

     const validateCycle = () => {
        if (ciclo === 'exploracion') {
            return(
                <div className="ciclo__container">
                    <ul className="ul__ciclos">
                        <StyledListForCycles>
                            <Cycle tema>Exploración</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('indagacion')}>
                            <Cycle>Indagación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('experimentacion')}>
                            <Cycle>Experimentación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('proyeccion')}>
                            <Cycle>Proyección</Cycle>
                        </StyledListForCycles>
                    </ul>
                    <div className="ciclo__posts">
                        <h3>Exploración</h3>
                        {postsExp.length > 0 ? postsExp.map((post) => {
                            return(
                                <div className="post_container" key={post.id} onClick={() =>openPost(post)}>
                                    <div className="post_image">
                                        <img src={post.image} alt="portada" width="250px" height="160px"/>
                                    </div>
                                    <div className="post_cover_text">
                                        <h2>{post.title}</h2>
                                        <span className="date-author_container">
                                            <p className="post_date"> <FontAwesomeIcon icon={faClock}/> {post.date}</p>
                                            <p className="post_author"><FontAwesomeIcon icon={faUser}/> {post.author}</p>
                                        </span>
                                        <p>{post.description}</p>
                                    </div>
                                </div>
                                )
                        })
                        :
                        <h2>Aun no hay publicaciones.</h2>
                        }
                    </div>
                </div>
            )
        }else if(ciclo === 'experimentacion'){
            return(
                <div className="ciclo__container">
                    <ul className="ul__ciclos">
                        <StyledListForCycles onClick={() => cambiarCiclo('exploracion')}>
                            <Cycle>Exploración</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('indagacion')}>
                            <Cycle>Indagación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles>
                            <Cycle tema>Experimentación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('proyeccion')}>
                            <Cycle>Proyección</Cycle>
                        </StyledListForCycles>
                    </ul>
                    <div className="ciclo__posts">
                        <h3>Experimentación</h3>
                        {postsExpm.length > 0 ? postsExpm.map((post) => {
                            return(
                                <div className="post_container" key={post.id} onClick={() =>openPost(post)}>
                                    <div className="post_image">
                                        <img src={post.image} alt="portada" width="250px" height="160px"/>
                                    </div>
                                    <div className="post_cover_text">
                                        <h2>{post.title}</h2>
                                        <span className="date-author_container">
                                            <p className="post_date"> <FontAwesomeIcon icon={faClock}/> {post.date}</p>
                                            <p className="post_author"><FontAwesomeIcon icon={faUser}/> {post.author}</p>
                                        </span>
                                        <p>{post.description}</p>
                                    </div>
                                </div>
                                )
                        })
                        :
                        <h2>Aun no hay publicaciones.</h2>
                        }
                    </div>
                </div>
            )
        }else if(ciclo === 'indagacion'){
            return(
                <div className="ciclo__container">
                    <ul className="ul__ciclos">
                        <StyledListForCycles onClick={() => cambiarCiclo('exploracion')}>
                            <Cycle>Exploración</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles>
                            <Cycle tema>Indagación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('experimentacion')}>
                            <Cycle>Experimentación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('proyeccion')}>
                            <Cycle>Proyección</Cycle>
                        </StyledListForCycles>
                    </ul>
                    <div className="ciclo__posts">
                        <h3>Indagación</h3>
                        {postsInd.length > 0 ? postsInd.map((post) => {
                            return(
                                <div className="post_container" key={post.id} onClick={() =>openPost(post)}>
                                    <div className="post_image">
                                        <img src={post.image} alt="portada" width="250px" height="160px"/>
                                    </div>
                                    <div className="post_cover_text">
                                        <h2>{post.title}</h2>
                                        <span className="date-author_container">
                                            <p className="post_date"> <FontAwesomeIcon icon={faClock}/> {post.date}</p>
                                            <p className="post_author"><FontAwesomeIcon icon={faUser}/> {post.author}</p>
                                        </span>
                                        <p>{post.description}</p>
                                    </div>
                                </div>
                                )
                        })
                        :
                        <h2>Aun no hay publicaciones.</h2>
                        }
                    </div>
                </div>
            )
        }else if(ciclo === 'proyeccion'){
            return(
                <div className="ciclo__container">
                    <ul className="ul__ciclos">
                        <StyledListForCycles onClick={() => cambiarCiclo('exploracion')}>
                            <Cycle>Exploración</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('indagacion')}>
                            <Cycle>Indagación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles onClick={() => cambiarCiclo('experimentacion')}>
                            <Cycle>Experimentación</Cycle>
                        </StyledListForCycles>
                        <StyledListForCycles>
                            <Cycle tema>Proyección</Cycle>
                        </StyledListForCycles>
                    </ul>
                    <div className="ciclo__posts">
                        <h3>Proyección</h3>
                        {postsProy.length > 0 ? postsProy.map((post) => {
                            return(
                                <div className="post_container" key={post.id} onClick={() =>openPost(post)}>
                                    <div className="post_image">
                                        <img src={post.image} alt="portada" width="250px" height="160px"/>
                                    </div>
                                    <div className="post_cover_text">
                                        <h2>{post.title}</h2>
                                        <span className="date-author_container">
                                            <p className="post_date"> <FontAwesomeIcon icon={faClock}/> {post.date}</p>
                                            <p className="post_author"><FontAwesomeIcon icon={faUser}/> {post.author}</p>
                                        </span>
                                        <p>{post.description}</p>
                                    </div>
                                </div>
                                )
                        })
                        :
                        <h2>Aun no hay publicaciones.</h2>
                        }
                    </div>
                </div>
            )
        }
    } 
        
    
         return(
            <>
                {ciclo.length === 0 ?
                    <StandardHome>
                        <ul className="ul__ciclos">
                            <StyledListForCycles onClick={() => cambiarCiclo('exploracion')}>
                                <Cycle>Exploración</Cycle>
                            </StyledListForCycles>
                            <StyledListForCycles onClick={() => cambiarCiclo('indagacion')}>
                                <Cycle>Indagación</Cycle>
                            </StyledListForCycles>
                            <StyledListForCycles onClick={() => cambiarCiclo('experimentacion')}>
                                <Cycle>Experimentación</Cycle>
                            </StyledListForCycles>
                            <StyledListForCycles onClick={() => cambiarCiclo('proyeccion')}>
                                <Cycle>Proyección</Cycle>
                            </StyledListForCycles>
                        </ul>
                        <HomeText>
                            <h1>Estudiantes de grado 5º.</h1>
                            <p>El presente ejercicio es un proceso académico el cual tiene como finalidad fortalecer los procesos lecto-escriturales, por tanto, algunas producciones pueden presentar situaciones de cohesión y coherencia, propias de los procesos de aprendizaje. Cabe resaltar que después de las revisiones y correcciones realizadas en el área se toma la decisión de no hacer modificaciones que alteren la esencia del trabajo.</p>
                            <Portada src={Portada2021} alt="" width="90%" height="550px"/>
                        </HomeText>
                    </StandardHome>
                    :
                    validateCycle()
                }
            </>
        )
}
const Portada = styled.img`
    border: 2px solid black;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin: 0 auto;
`
const HomeText = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
    margin-bottom: 50px;
    text-align: center;
    font-family: 'Roboto', sans-serif;
    h1{
        font-size: 2.5rem;
        font-weight: bold;
        color: #2D2D2D;
    }
    p{
        font-size: 1.5rem;
        color: #2D2D2D;
        margin: 10px;
    }
`

const StandardHome = styled.div`
    display: flex;
`

const StyledListForCycles = styled.li`
    color: #009DD2;
    font-family: 'Varela Round', sans-serif;
    cursor: pointer;
    margin-right: 10px;
    display: flex;
    align-items: center;
    justify-content: left;
    height: fit-content;
    &::before{
        content: 'X';
    }
`

const Cycle = styled.h3`
    color: #FFFFFF;
    font-size: 22px;
    text-decoration: ${props => props.tema ? 'underline 3px' : 'none'};
    text-shadow: ${props => props.tema ? '1px 3px 0 #969696, 1px 0 5px #aba8a8': '1px 1px 1px #000'};
    height: fit-content;
    &:hover{
        transition: 150ms;
        opacity: 0.5;
    }
`

export default Ciclos